@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.qodef-comment-holder {
    @include qodefRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .qodef-comment-holder-inner {
        padding: 32px 0 20px;
    }
    
    .qodef-comments-title {
        margin-bottom: 30px;
        font-size: 35px;
        line-height: 1.14em;
    }

    .qodef-comment {

        &.qodef-no-avatar {

            .qodef-comment-text {
                padding: 0;
            }
        }
    }

    .qodef-comment-list {
        position: relative;
        padding: 0;
        margin: 0;
        list-style: none;
        z-index: 150;

        li {
            @include qodefRelativeHolderLayout();
            margin: 25px 0 0;
            padding: 11px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 107px;
        }
    }

    .qodef-comment-image {
        position: relative;
        top: 0;
        left: 0;
        width: 83px;
        height: 83px;
        display: block;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .qodef-comment-text {
        padding: 0 0 0 108px;
        min-height: 82px;

        .qodef-comment-info {
            @include qodefRelativeHolderLayout();
            margin: 0 0 4px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            position: relative;
            float: right;
            margin-left: 10px;
            font-size: 17px;
            line-height: 1.47em;
            color: $default-heading-color;
            text-transform: capitalize;

            &:hover {
                color: $first-main-color;
            }
            
            &:after {
                content: '';
                width: 100%;
                height: 1px;
                background-color: currentColor;
                position: absolute;
                bottom: 2px;
                left: 0;
            }
        }

        .qodef-comment-name {
            float: left;
            margin: 0;
            font-size: 20px;
            line-height: 1.4em;
        }

        .qodef-comment-date {
            float: left;
            margin: 0;
            font-size: 15px;
            line-height: 1.66em;
        }

        .qodef-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 20px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 14px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.qodef-comment-form {
    @include qodefRelativeHolderLayout();
    margin: 54px 0 40px;
    clear: both;

    body:not([class*="struktur-core"]) & {
        margin-top: 18px;
    }

    .comment-reply-title {
        margin: 0 0 11px;
        font-size: 35px;
        line-height: 1.14em;
    }

    .logged-in-as {
        margin: 0 0 20px;
    }
}

.qodef-comment-form,
.qodef-comment-holder {
    input#author,
    input#email {
        width: calc(50% - 12px);
        float: left;
    }
    
    input#author {
        margin-right: 12px;
    }
    
    input#email {
        margin-left: 12px;
    }
    
    textarea#comment {
        height: 40px;
        font-size: 15px;
        line-height: 24px;
        font-weight: 400;
        color: $default-text-color;
    }
    
    .comment-form-cookies-consent {
        margin: 0 0 15px;
    }
}

@include ipad-portrait {

    .qodef-comment-holder {

        .qodef-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .qodef-comment-holder {

        .qodef-comment-list {

            .children {
                padding: 0;
            }
        }

        .qodef-comment-text {

            .qodef-comment-date,
            .qodef-comment-name {
                float: left;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: right;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {

    .qodef-comment-holder {

        .qodef-comment-image {
            float: none;
        }

        .qodef-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.qodef-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }
    
    
    &.wpcf7-textarea {
        position: relative;
        width: 100%;
        margin: 0 0 18px;
        padding: 10px 10px 3px 0;
        font-family: inherit;
        font-size: 15px;
        line-height: 1.33em;
        font-weight: 300;
        color: $default-text-color;
        background-color: transparent;
        border-width: 0;
        border-bottom: 2px solid $default-heading-color;
        border-radius: 0;
        outline: 0;
        cursor: pointer;
        -webkit-appearance: none;
        box-sizing: border-box;
        transition: border-color 0.2s ease-in-out;
    
        &:focus {
            color: $default-heading-color;
            border-color: $default-text-color;
        }
    
        @include qodefPlaceholder {
            color: inherit;
        }
    }
}

//#submit_comment, // added our button classes in comments.php
//input.wpcf7-form-control.wpcf7-submit, // added our button classes in contact-form-7-confing.php
.post-password-form input[type='submit']{
    @include qodefButtonDefaultStyle();
    @include qodefButtonSize();
    @include qodefButtonSolidColor();

    cursor: pointer;

    &:hover {
        @include qodefButtonSolidHoverColor();
    }
}

#respond input[type="text"]:last-of-type:not(:last-child) {
    margin-bottom: 0;
}

p.form-submit,
.wpcf7-form p {
    margin: 0;
}

#respond textarea,
.qodef-style-form textarea,
.wpcf7-form textarea {
    display: block;
}

.qodef-two-columns-form-without-space {
    .wpcf7-form .ajax-loader {
        display: none;
    }
}

/* contact form 7 - start */

.qodef-newsletter-custom-form {
    position: relative;
    
    input.wpcf7-form-control {
        padding-right: 50px;
        border-bottom-color: $first-main-color;
        color: $first-main-color;
        
        @include qodefPlaceholder {
            color: $first-main-color;
        }
    
        &:focus {
            color: $first-main-color;
            border-bottom-color: $first-main-color;
        }
    }
    
    button.wpcf7-submit {
        &.qodef-btn {
            position: absolute;
            right: 0;
            top: 13px;
            width: 20px;
            padding: 0;
            font-size: 0;
            color: transparent;
            background-color: transparent;
            border: none !important;
    
            &:after {
                content: "\f0e0";
                font-family: "Font Awesome 5 Free";
                font-size: 17px;
                color: $first-main-color;
            }
    
            &:hover {
                background-color: transparent !important;
                color: transparent !important;
                border: none !important;
            }
        }
    }
    
    .ajax-loader {
        display: none;
    }
}

.wpcf7-form {
    .wpcf7-response-output {
        margin-top: 0;
    }
}

.qodef-page-footer {
    .qodef-newsletter-custom-form {
    
        input.wpcf7-form-control {
            border-bottom-color: #fff;
            color: #fff;
    
            @include qodefPlaceholder{
                color: #fff;
            }
        }
    
        button.wpcf7-submit {
            &.qodef-btn {
                &:after {
                    color: #fff;
                }
            }
        }
    }
}

input::-ms-clear {
    display: none;
}

/* contact form 7 - end */

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.qodef-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.qodef-slider-is-moving {
        .owl-item {
            .qodef-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.qodef-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                transform: translateY($pos);
            }
        }
    }

    &.qodef-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            padding: 0;
            margin: 0;
            color: $default-heading-color;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;

            transform: translateY(-50%);
            transition: color .2s ease-out;

            &:hover {
                color: $first-main-color;
            }

            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-size: 46px;
                line-height: 1;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            left: 30px;
        }

        .owl-next {
            right: 30px;
        }
    }

    .owl-dots {
        text-align: center;
        margin: 30px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            padding: 0;
            margin: 0 25px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            overflow: visible;
            -webkit-appearance: none;

            span {
                display: inline-block;
                vertical-align: middle;
                width: 6px;
                height: 6px;
                border-radius: 3em;
                background-color: #ccc;
                box-sizing: border-box;
                transition: background-color .2s ease-out;
            }

            &:hover,
            &.active {

                span {
                    background-color: #333;
                    position: relative;
                    
                    &:after {
                        content: '';
                        position: absolute;
                        left: -13px;
                        top: -13px;
                        width: 30px;
                        height: 30px;
                        border: 1px solid #ccc;
                        border-radius: 3em;
                    }
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .qodef-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .qodef-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                transition: border-color 0.2s ease-out;
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.qodef-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .qodef-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
    .qodef-wrapper {
        padding: 0 !important;
    }

    .qodef-content {
        height: 100vh;
    }

    &.admin-bar {
        .qodef-content {
            height: calc(100vh - 32px);
        }
    }

    .qodef-content-inner {
        @include qodefTableLayout();
    }

    .qodef-page-not-found {
        @include qodefTableCellLayout();

        padding: 0 20%;
        text-align: center;

        .qodef-404-title {
            margin: 0;
        }

        .qodef-404-subtitle {
            margin: 35px 0 0;
        }

        .qodef-404-text {
            margin: 10px 0 0;
        }

        .qodef-btn {
            margin: 30px 0 0;

            &.qodef-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }

    @include ipad-landscape {
        .qodef-content {
            height: 100%;
            min-height: 100vh;
        }
    
        .qodef-content-inner {
            height: calc(100vh - 70px);
        }

        &.admin-bar {
            .qodef-content {
                height: 100%;
                min-height: calc(100vh - 32px);
            }
        }
    }

    @include ipad-portrait {
        .qodef-page-not-found {
            padding: 70px 30px 20px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#qodef-back-to-top {
    position: fixed;
    width: auto;
    right: -5px;
    bottom: 71px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    %line {
        content: '';
        display: block;
        position: absolute;
        left: 0px;
        bottom: 0px;
        width: 100%;
        height: 1px;
        background-color: currentColor;
    }

    .qodef-back-to-top-text {
        display: block;
        position: relative;
        transform: rotate(-90deg);
        font-size: 20px;
        color: $first-main-color;
        
        &.qodef-back-to-top-light-skin {
            color: #fff;
        }
        
        &.qodef-back-to-top-dark-skin {
            color: $default-heading-color;
        }
    
        &:before {
            @extend %line;
            @include qodefTransform(scaleX(0));
            transform-origin: 0 0;
        }

        &:after {
            @extend %line;
            transform-origin: 100% 0;
        }
    }

    &.off {
        opacity: 0;
        visibility: hidden;

        transition: opacity 0.15s ease, visibility .0s .15s;
    }

    &.on {
        opacity: 1;
        visibility: visible;

        transition: opacity 0.3s ease;
    }

    &:hover {
        .qodef-back-to-top-text {
            &:before {
                @include qodefTransform(scaleX(1));
                transition: transform 1.1s cubic-bezier(0.19, 1, 0.22, 1) .3s;
            }
            
            &:after {
                @include qodefTransform(scaleX(0));
                transition: transform 1.1s cubic-bezier(0.19, 1, 0.22, 1);
            }
        }
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.qodef-self-hosted-video-holder {
    .qodef-video-wrap {
        .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}

/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

/* ==========================================================================
   #Pattern styles - end
   ========================================================================== */

.qodef-pattern-holder {
    position: relative;
    
    .qodef-pattern-before,
    .qodef-pattern-after {
        display: block;
        content: '';
        height: 100%;
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
    }
}

.qodef-pattern-overlay {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}


/* ==========================================================================
   #Pattern styles - end
   ========================================================================== */

/* ROW BACKGROUND TEXT - start */

.vc_row,
.qodef-elementor-row {
    &.qodef-row-has-background-text {
        position: relative;
    }
}

.qodef-row-background-text-holder {
    @include qodefAbsoluteHolderLayout();
    z-index: -1;
    white-space: nowrap;
    
    .qodef-row-background-text-wrapper{
        @include qodefTableLayout();
        
        .qodef-row-background-text-wrapper-inner{
            display: table-cell;
            vertical-align: middle;
            color: #f7f7f7;
            font-size: 150px;
            font-family: $default-text-font;
            
            .qodef-row-background-text-1,
            .qodef-row-background-text-2{
                font-weight: 700;
                line-height: 1;
                letter-spacing: -0.04em;
            }
        }
        
        @include ipad-landscape{
            display: none;
        }
        
        &.qodef-row-background-text-animation {
            visibility: hidden;
            
            .no-touch & {
                .qodef-char-mask {
                    display: inline-block;
                    vertical-align: middle;
                    overflow: hidden;
                    line-height: 1.15em;
                    letter-spacing: -0.04em;
                    
                    > span {
                        display: inline-block;
                        transform: scaleX(-1);
                        opacity: 0;
                    }
                }
                
                .qodef-char-mask > span.qodef-show {
                    transform: scaleX(1);
                    opacity: 1;
                    transition: transform .85s cubic-bezier(0.19, 1, 0.22, 1), opacity 1s cubic-bezier(0, 0, 0, 1);
                }
            }
        }
    }
}

/* ROW BACKGROUND TEXT - end */

/* Portfolio Archive - start */

.archive {
    &.tax-portfolio-category {
    
    
        .qodef-content {
            .qodef-content-inner {
                >.qodef-container >.qodef-container-inner,
                >.qodef-full-width>.qodef-full-width-inner {
                    padding-top: 0;
                    padding-bottom: 110px;
                }
            }
        }
    }
}

/* Portfolio Archive - end */

.qode-light-link {
    a {
        color: #fff;
        transition: opacity .3s ease-in-out;
        
        &:hover {
            color: #fff;
            opacity: .85;
        }
    }
}